import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './components/layout/layout.component';
import { AuthGuard } from './guards/auth.guard';
import { SetupGuard } from './guards/setup.guard';


const routes: Routes = [
  {
    path: '',
    component: LayoutComponent,
    canActivate: [
      AuthGuard
    ],
    children: [
      { 
        path: 'ticket', 
        loadChildren: () => import('./ticket/ticket.module').then(m => m.TicketModule) 
      },
      { path: 'itsm', loadChildren: () => import('./itsm/itsm.module').then(m => m.ItsmModule) },
      { path: 'cmdb', loadChildren: () => import('./cmdb/cmdb.module').then(m => m.CmdbModule) },
      { path: 'workflow', loadChildren: () => import('./wf/wf.module').then(m => m.WfModule) },
      { path: 'admin', loadChildren: () => import('./admin/admin.module').then(m => m.AdminModule) },
      { path: 'profile', loadChildren: () => import('./profile/profile.module').then(m => m.ProfileModule) },
    ]
  },
  { path: 'portal', loadChildren: () => import('./portal/portal.module').then(m => m.PortalModule) },
  { 
    path: 'auth',
    // canActivate: [
    //   SetupGuard
    // ],
    loadChildren: () => import('./auth/auth.module').then(m => m.AuthModule) 
  },
  { path: 'setup', loadChildren: () => import('./setup/setup.module').then(m => m.SetupModule) },
  


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
